import React, { Component } from "react"
import PWHImg from "../components/gatsbyimg/pamwelchdoccomhero"

const Project = props => {
  const imageURL = props.data.projectImageURL ?? props.data.projectURL
  return (
    <div className="level">
      <div className="project">
        <div className="proj-details">
          <a
            href={props.data.projectURL}
            target="_blank"
            rel="noreferrer"
            style={{ color: "inherit" }}

          >
            <h3>{props.data.projectName}</h3>
          </a>

          <p>{props.data.projectDescritption}</p>

          <a href={props.data.projectURL} target="_blank" rel="noreferrer"><button className="button is-link">See this project</button></a>

        </div>

        <div className="proj-image">
          <a href={imageURL} target="_blank" rel="noreferrer">
            <PWHImg />
          </a>
        </div>
      </div>
    </div>
  )
}

export default class projectList extends Component {
  constructor(props) {
    super()
    this.state = {
      projectName: "pamwelch.com",
      projectURL: "http://www.pamwelch.com",
      projectDescritption:
        ' "pamwelch.com" is a professional portfolio website built using React and Gatsby. The site was created to showcase professional marketing projects created by Pam Welch of Troy, New York. The site itself contains the stylings and features of a modern minimal portfolio site, including google analytics and lazy loading of images.',
    }
  }

  createList() {
    return <Project data={this.state} />
  }

  render() {
    return (<div>
      <h1>Projects</h1>
      {this.createList()}
      </div>
      )
  }
}
